import 'script!uglify!./vendor/jquery.cookie.js';
import 'script!uglify!./vendor/fitvids.js';
import 'script!uglify!./vendor/jquery.scrollDepth.js';
import 'script!uglify!./vendor/jquery.scrollTo.js';
import 'script!uglify!./vendor/jquery.sticky-kit.js';
import 'script!uglify!./vendor/enquire.js';
import 'script!uglify!./modules/jquery.cookie.lk.js'
import 'script!uglify!./modules/headerHero.js'
import 'script!uglify!./modules/formCleanup.js'
import 'script!uglify!./modules/formPlaceholder.js'
import 'script!uglify!./modules/accordionTabs.js'
import 'script!uglify!./modules/modal.js'
import 'script!uglify!./modules/homePageUaTracking.js'
import 'script!uglify!./modules/ajaxPagination.js'


// Track Scroll Depth as Google Analytics Events
$(function() {
  $.scrollDepth({
    pixelDepth: false
  });
});

// Jump Link on Integrations Page
$(function() {
  $("#scrollToLogos").on('click', function() {
    $.scrollTo(".integrations-logos-before", 500);
  });
});

import 'script!../vendor/magnific/jquery.magnific-popup.min.js';

// Sticky sidebar
$(function() {
  enquire.register('screen and (max-width: 767px)', {
    match: function() {
      $('.js-sticky-sidebar').trigger("sticky_kit:detach");
    }
  });
  enquire.register('screen and (min-width: 768px) and (max-width: 1024px)', {
    match: function() {
      $('.js-sticky-sidebar').trigger("sticky_kit:detach");
      $('.js-sticky-sidebar').stick_in_parent({
        offset_top: 20.25
      });
    }
  });
  enquire.register('screen and (min-width: 1025px)', {
    match: function() {
      $('.js-sticky-sidebar').trigger("sticky_kit:detach");
      $('.js-sticky-sidebar').stick_in_parent({
        offset_top: 68 + 20.25
      });
    }
  });
});

// Ignore clicks
$(function() {
  $(".js-noclick").click(function(e) {
    e.preventDefault();
  });
});

// Blog Functions
$(function() {
  $(document.body).on('change', 'select.js-link-dropdown', function(event) {
      var url = $(this).val();
      if(url) window.location = url;
    })
    .on('submit', '.blog-search form', function(event) {
      var terms = $(this).find('input[name="s"]');
      if(terms.val().trim() == '') event.preventDefault();
    });
});



